import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { QuillModule } from 'ngx-quill';
import { AuthSetupQuillContainerComponent } from './auth-setup-quill-container.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const COMPONENTS = [AuthSetupQuillContainerComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatFormFieldModule];
const TEI_MATERIAL_MODULES = [QuillModule, TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [...COMPONENTS]
})
export class AuthSetupQuillContainerModule { }
