import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import
{
  GetAuthenticationSettingsRequest,
  GetAuthenticationSettingsResponse
} from '../@intermediate/setup-authentication-settings/model/get-setup-authentication-settings.model';
import
{
  PostAuthenticationSettingsRequest,
  PostAuthenticationSettingsResponse
} from '../@intermediate/setup-authentication-settings/model/post-setup-authentication-settings.model';
import
{
  ISetupAuthenticationSettingInterface
} from '../@intermediate/setup-authentication-settings/setup-authentication-settings.interface';

@Injectable({
  providedIn: 'root'
})
export class SetupAuthenticationSettingsService implements ISetupAuthenticationSettingInterface
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/AuthenticationSettings';
  }

  Get(request: GetAuthenticationSettingsRequest): Observable<GetAuthenticationSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetAuthenticationSettings';
    return this.http.get<GetAuthenticationSettingsResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostAuthenticationSettingsRequest): Observable<PostAuthenticationSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/PostAuthenticationSettings';
    return this.http.post<PostAuthenticationSettingsResponse>(apiUrl, request);
  }
}
