import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { AuthenticationSetupSettingsComponent } from './authentication-setup-settings.component';
import { SetupAuthenticationSettingsService } from '../../@services/setup-authentication-settings.service';
import {
  AuthSetupQuillContainerModule
} from '../../components/auth-setup-quill-container/auth-setup-quill-container.module';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { AuthenticationSetupSettingsRoutingModule } from './authentication-setup-settings-routing.module';

const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
  AuthenticationSetupSettingsRoutingModule
];
const MATERIAL_MODULES = [
  MatCheckboxModule,
  MatSelectModule,
  MatSlideToggleModule,
  MatInputModule,
  MatRadioModule
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule,
  AuthSetupQuillContainerModule
];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [AuthenticationSetupSettingsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'ISetupAuthenticationSettingInterface', useClass: SetupAuthenticationSettingsService }],
})
export class AuthenticationSetupSettingsModule { }
