import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import
{
  GetAuthenticationSettingsResponse
} from '../../@intermediate/setup-authentication-settings/model/get-setup-authentication-settings.model';
import
{
  PostAuthenticationSettingsRequest
} from '../../@intermediate/setup-authentication-settings/model/post-setup-authentication-settings.model';
import
{
  ISetupAuthenticationSettingInterface
} from '../../@intermediate/setup-authentication-settings/setup-authentication-settings.interface';

@Component({
  selector: 'tei-portal-admin-center-authentication-setup-settings',
  templateUrl: './authentication-setup-settings.component.html',
  styleUrls: ['./authentication-setup-settings.component.scss'],
})
export class AuthenticationSetupSettingsComponent implements OnInit, OnDestroy
{
  isMobile: boolean;
  form!: FormGroup;
  isLoading: boolean = false;
  breadCrumbData: BreadCrumb[] = [];
  publicRegistrationEnabled: boolean = false;
  memberActivationEnabled: boolean = false;
  memberActivationMessage: string = this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.AUTHENTICATION_SETTING.MEMBER_ACTIVATION_MESSAGE_PLACEHOLDER');
  private subscription: Subscription = new Subscription();
  private emptyQuillPatterns = [
    /^<p[^>]*>\s*<br>\s*<\/p>$/i,
    /^<p[^>]*>\s*<\/p>$/i,
    /^<p[^>]*><\/p>$/i,
    /^<div[^>]*>\s*<br>\s*<\/div>$/i
  ];

  constructor(
    private fb: FormBuilder,
    private translate: TranslateService,
    @Inject('ISetupAuthenticationSettingInterface') private service: ISetupAuthenticationSettingInterface,
    private snackBar: SnackBarService,
    private responsiveService: ResponsiveService
  ) { }

  ngOnInit()
  {
    this.createBreadcrumb();
    this.initializeForm();
    this.getSettings();
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm';
  }

  private createBreadcrumb(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
        relativeUrl: '',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SETUP'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.AUTHENTICATION_SETTING.TITLE'),
        relativeUrl: '',
      },
    ];
  }

  initializeForm(): void
  {
    this.form = this.fb.group({
      loginMessage: [''],
      linkToDisplayFirst: ['PublicRegistration', Validators.required],
      publicRegistrationLabel: ['Public Registration', Validators.maxLength(35)],
      memberActivationLabel: ['Member Activation', Validators.maxLength(35)],
      publicRegistrationMessage: [''],
      memberActivationMessage: [''],
    });
  }

  getSettings(): void
  {
    this.isLoading = true;
    this.subscription.add(
      this.service.Get({}).subscribe({
        next: (response: GetAuthenticationSettingsResponse) =>
        {
          this.handleSettingsResponse(response);
          this.isLoading = false;
        },
        error: () =>
        {
          this.isLoading = false;
        },
      })
    );
  }

  private handleSettingsResponse(response: GetAuthenticationSettingsResponse): void
  {
    this.publicRegistrationEnabled = response.PublicRegistrationEnabled;
    this.memberActivationEnabled = response.MemberActivationEnabled;

    this.form.patchValue({
      loginMessage: response.LoginMessage || '',
      linkToDisplayFirst: response.LinkToDisplayFirst,
      publicRegistrationLabel: response.PublicRegistrationLabel,
      memberActivationLabel: response.MemberActivationLabel,
      publicRegistrationMessage: response.PublicRegistrationMessage || '',
      memberActivationMessage: response.MemberActivationMessage ? response.MemberActivationMessage : this.memberActivationMessage
    });

    this.updateFormControlsState();
  }

  private updateFormControlsState(): void
  {
    const prLabel = this.form.get('publicRegistrationLabel');
    const prMessage = this.form.get('publicRegistrationMessage');
    const maLabel = this.form.get('memberActivationLabel');
    const maMessage = this.form.get('memberActivationMessage');

    if (!this.publicRegistrationEnabled)
    {
      prLabel?.disable();
      prMessage?.disable();
      prLabel?.setValidators(Validators.maxLength(35));
    } else
    {
      prLabel?.enable();
      prMessage?.enable();
      prLabel?.setValidators([Validators.required, Validators.maxLength(35)]);
    }
    prLabel?.updateValueAndValidity();

    if (!this.memberActivationEnabled)
    {
      maLabel?.disable();
      maMessage?.disable();
      maLabel?.setValidators(Validators.maxLength(35));
    } else
    {
      maLabel?.enable();
      maMessage?.enable();
      maLabel?.setValidators([Validators.required, Validators.maxLength(35)]);
    }
    maLabel?.updateValueAndValidity();
  }

  save(form: FormGroup): void
  {
    if (form.invalid)
    {
      this.markFormGroupTouched(form);
      return;
    }

    this.isLoading = true;

    const request: PostAuthenticationSettingsRequest = {
      LoginMessage: this.hasTextContent(form.value.loginMessage) ? form.value.loginMessage : null,
      LinkToDisplayFirst: form.value.linkToDisplayFirst,
      PublicRegistrationLabel: this.publicRegistrationEnabled ? form.value.publicRegistrationLabel : undefined,
      MemberActivationLabel: this.memberActivationEnabled ? form.value.memberActivationLabel : undefined,
      PublicRegistrationMessage: this.publicRegistrationEnabled ? (this.hasTextContent(form.value.publicRegistrationMessage) ? form.value.publicRegistrationMessage : null) : undefined,
      MemberActivationMessage: this.memberActivationEnabled ? (this.hasTextContent(form.value.memberActivationMessage) ? form.value.memberActivationMessage : null) : undefined,
    };

    this.subscription.add(
      this.service.Post(request).subscribe({
        next: () =>
        {
          this.snackBar.showSuccess(this.translate.instant("ADMIN_CENTER.AUTHENTICATION.SUCCESS_MESSAGE"), '', null, 'left');
          this.isLoading = false;
        },
        error: () =>
        {
          this.isLoading = false;
        },
      })
    );
  }

  private markFormGroupTouched(formGroup: FormGroup): void
  {
    Object.values(formGroup.controls).forEach((control) =>
    {
      control.markAsTouched();
      if (control instanceof FormGroup)
      {
        this.markFormGroupTouched(control);
      }
    });
  }

  hasTextContent(content: string | null | undefined): boolean
  {
    if (!content) return false;

    if (this.emptyQuillPatterns.some(pattern => pattern.test(content)))
    {
      return false;
    }

    const div = document.createElement('div');
    div.innerHTML = content;
    const textContent = div.textContent || div.innerText || '';

    return textContent.trim().length > 0;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
