import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormGroupDirective } from '@angular/forms';
import * as Quill from 'quill';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'admin-center-auth-quill-container',
  templateUrl: './auth-setup-quill-container.component.html',
  styleUrls: ['./auth-setup-quill-container.component.scss'],
})
export class AuthSetupQuillContainerComponent implements OnInit
{
  @Input() ContentHeader: string = '';
  @Input() ContentDescription: string = '';
  @Input() ControlName: string = '';

  form!: FormGroup;
  quillModules = {
    toolbar: [
      ['bold', 'italic', 'underline'],
      [{ 'align': [] }],
      ['link'],
      ['clean']
    ],
    syntax: false,
  };

  constructor(private rootFormGroup: FormGroupDirective,
              private translate: TranslateService,)
  {
    var FontStyle = Quill.import('attributors/style/font');
    var FontClass = Quill.import('attributors/class/font');
    var AlignClass = Quill.import('attributors/class/align');
    var AlignStyle = Quill.import('attributors/style/align');

    var DirectionClass = Quill.import('attributors/class/direction');
    var DirectionStyle = Quill.import('attributors/style/direction');
    var DirectionAttribute = Quill.import('attributors/attribute/direction');

    Quill.register(DirectionAttribute, true);
    Quill.register(AlignClass, true);
    Quill.register(DirectionClass, true);
    Quill.register(FontClass, true);
    Quill.register(AlignStyle, true);
    Quill.register(DirectionStyle, true);
    Quill.register(FontStyle, true);
  }

  ngOnInit(): void
  {
    this.form = this.rootFormGroup?.control as FormGroup;
  }

  getFormControlByName(name: string): FormControl
  {
    return this.form.get(name) as FormControl;
  }
}
